/* MAIN */
* { margin: 0; padding: 0; -webkit-box-sizing: border-box; box-sizing: border-box; font-family: Roboto; }
html { font-size: 16px; }
img { max-width: 100%; }
h1,h2,h3,h4,h5,h6 { margin: 10px; font-weight: 300; }
p { font-size: 1rem; font-family: Roboto; font-weight: 300; margin: 10px; }
#root { margin: 0 auto; max-width: 1000px; min-height: 100vh; }
.white { background: #fff; }
.vcenter { display: -ms-flexbox; display: flex; -ms-flex-align: center; align-items: center; -ms-flex-line-pack: center; align-content: center; }
.center { text-align: center; }
.email span:nth-child(2):after { content: "@"; }
.btn { border-radius: 5px; color: #fff; background-color: #5cb85c; font-family: Roboto; font-weight: 400; display: inline-block; padding: 15px; border: 1px solid #4cae4c; }
.inner-panel { margin: 20px; border-radius: 5px; padding: 40px; }
a, a:visited, a:active, a.hover { text-decoration: none; color: inherit; }
::-webkit-scrollbar { display: none; opacity: 0; visiblity: hidden; }

/* Background */
.background { z-index: -1; position: absolute; top: 0; bottom: 0; left: 0; right: 0; height: 100%; width: 100%; background-color: #000; overflow: hidden; }
.background.gradient { background: -webkit-radial-gradient(circle farthest-corner at 0% -30%, #f00 0%, #000 50%); background: -o-radial-gradient(circle farthest-corner at 0% -30%, #f00 0%, #000 50%); background: radial-gradient(circle farthest-corner at 0% -30%, #f00 0%, #000 50%); }
.background div { will-change: transform; }
.background div:nth-child(1) { width: 1px; height: 1px; }
.background div:nth-child(2) { width: 1px; height: 1px; top: -2000px; position: absolute; }
.background div:nth-child(3) { width: 2px; height: 2px; }
.background div:nth-child(4) { width: 2px; height: 2px; top: -2000px; position: absolute; }
.background div:nth-child(5) { width: 3px; height: 3px; } 
.background div:nth-child(6) { width: 3px; height: 3px; top: -2000px; position: absolute; } 
@-webkit-keyframes stars { from { -webkit-transform: translate3d(0,2000px,0); transform: translate3d(0,2000px,0); } to { -webkit-transform: translate3d(0,0px,0); transform: translate3d(0,0px,0); } } 
@keyframes stars { from { -webkit-transform: translate3d(0,2000px,0); transform: translate3d(0,2000px,0); } to { -webkit-transform: translate3d(0,0px,0); transform: translate3d(0,0px,0); } }

/* Header */
.header { height: 16vh; line-height: 16vh; }
.header svg { width: 100%; height: 100%; }
.header { background: -webkit-gradient(linear, left bottom, left top, from(rgba(0,0,100,0.3)), color-stop(50%, rgba(0,0,0,0))); background: -webkit-linear-gradient(bottom, rgba(0,0,100,0.3) 0%, rgba(0,0,0,0) 50%); background: -o-linear-gradient(bottom, rgba(0,0,100,0.3) 0%, rgba(0,0,0,0) 50%); background: linear-gradient(0deg, rgba(0,0,100,0.3) 0%, rgba(0,0,0,0) 50%); }
.header svg text { font-family: 'Pirata One'; font-size: 5rem; }
.header svg > text { text-shadow: 0px 0px 5px #fff; }

/* Nav */
.nav { height: 10vh; color: #fff; } 
.nav ul { list-style: none; display: -ms-flexbox; display: flex; -ms-flex-direction: row; flex-direction: row; -ms-flex-wrap: nowrap; flex-wrap: nowrap; -ms-flex-align: center; align-items: center; -ms-flex-line-pack: center; align-content: center; height: 100%; }
.nav li { background: #181a1b; font-size: 1.2rem; cursor: pointer; -ms-flex-positive: 1; flex-grow: 1; -ms-flex-preferred-size: 0; flex-basis: 0; -webkit-transition: 0.75s; -o-transition: 0.75s; transition: 0.75s; text-align: center; height: 100%; line-height: 10vh; position: relative; }
.nav li.active { cursor: auto; color: #fff; background: #5a5d5f; }
.nav li:hover:not(.active) { color: orange; }

/* CONTENT */
#content { height: 71vh; overflow: visible; }
.content { height: 100%; background: #5a5d5f; color: #f0f0f0; overflow: auto; } 
.content li { font-weight: 300; margin: 10px; }

/* FOOTER */
.footer { height: 3vh; text-align: center; }
.footer-content { line-height: 3vh; color: #f0f0f0; background: #333; font-size: 0.875rem; }

/* GRID */
.half,.third,.fourth { display: inline-block; vertical-align: top; }
.one { width: 100%; }
.half { width: 50%; } 
.third { width: 33.3%; } 
.fourth { width: 25%; }

/* MEDIA QUERIES */
@media screen and (max-width: 900px) {
  .fourth { width: 50%; }
  .header > div { font-size: 2rem; }
}

@media screen and (max-width: 600px) {
  html { font-size: 12px; }
  .half, .third, .fourth { width: 100%; display: block; }
}

/* Carousel */
.carousel-wrapper { position: relative; -webkit-perspective: 1000px; perspective: 1000px; width: 100%; height: 100%; }
.carousel { width: 100%; height: 100%; position: absolute; -webkit-transform-style: preserve-3d; transform-style: preserve-3d; }
.carousel > div { position: absolute; width: 100%; height: 100%; -webkit-backface-visibility: hidden; backface-visibility: hidden; }

/* Bio */
.bio p { padding: 10px; line-height: 1.5rem; font-size: 0.95rem; }
.bio img { max-width: calc(100% - 100px); border-radius: 5px; border: 1px solid #202020; margin: 30px 20px 0 20px; }

/* Projects */
.project p { font-size: 0.9rem; }
.project ul li { font-size: 0.8rem; list-style: none; display: inline-block; background: #f0f0f0; border-radius: 5px; color: #5a5f5d; padding: 5px; margin: 3px 5px; font-weight: 400; }

/* Skills */
.category { padding: 10px 10px 0 10px; }
.category ul li { list-style: none; padding-left: 20px; }
.content.skills .inner-panel img { width: calc(100% - 20px); }

/* Experience */
.job { padding: 12px; } 
.job h3 > span { font-size: 1rem; font-style: italic; font-weight: 300; }
.job p { font-size: 0.9rem; }

/* Education */
.school { padding: 10px; }
.school ul { list-style: none; padding-left: 20px; font-size: 0.9rem; font-weight: 300; }



